<template>
    <div class="container-fluid">
        <div class="row mt-4">
            <div class="col-md table-responsive">
                <table class="table align-middle table-hover ">
                    <thead>
                        <tr class="tabletr">
                            <th>圖片</th>
                            <th>商品名稱</th>
                            <th>價格</th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for=" item in productsdata" :key="item.id" class="tabletr">
                            <td style="width: 200px">
                                <div style="height: 100px; background-size: cover; background-position: center" :style="{backgroundImage: `url(${item.imagesUrl})`}"></div>
                            </td>
                            <td><a href="#" class="text-dark">{{item.title}}</a></td>
                            <td>
                                <!-- <div class="h5"> 元</div> -->
                                <del class="h6">原價 {{item.origin_price}}元</del>
                                <div class="h5" >現在只要 {{item.price}}元</div>
                            </td>
                            <td>
                                <div class="btn-group btn-group-sm">
                                    <button type="button" class="btn btn-outline-secondary" @click.prevent="getproduct(item.id)">
                                        查看更多
                                    </button>
                                    <button type="button" class="btn btn-outline-danger">
                                        <!-- <div class="spinner-grow text-danger spinner-grow-sm" role="status">
                                            <span class="visually-hidden">Loading...</span>
                                        </div> -->
                                        加到購物車
                                    </button>
                                </div>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
    <Loading :active="isLoading"></Loading>
</template>
<style>
    .tabletr td:nth-child(1),th:nth-child(1){
        max-width: 100px;
        overflow-x: auto;
    }
    .tabletr td:nth-child(3),th:nth-child(3){
        max-width: 100px;
        overflow-x: auto;
    }
    .tabletr td:nth-child(4) div,th:nth-child(4){
        flex-wrap: wrap;
    }
@media(max-width: 414px){
    .tabletr td:nth-child(3),th:nth-child(3){
        display: none;
    }
}

</style>
<script>
export default {
    data () {
        return {
            productsdata: [],
            isLoading: false
        };
    },
    methods: {
        getproducts () {
            this.isLoading = true;
            const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/products`;
                this.$http.get(api)
                .then((res) => {
                    console.log(res);
                    if (res.data.success) {
                        this.isLoading = false;
                        this.productsdata = res.data.products;
                    }
                });
        },
        getproduct (id) {
            this.$router.push(`/user/product/${id}`);
        }
    },
    created () {
        this.getproducts();
    }
};
</script>
